* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  background-color: #f0f0f0;
}

a {
  color: #fff;
  text-decoration: none;
}

.container {
  padding: 1rem;
}

.btn {
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
  outline: none;
}

.btn:hover {
  background: #666;
}

.about {
  margin: 1rem;
  padding: 1rem;
  background-color: #fff;
  box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.1);
}

